.page {
  font-family: "Roboto", sans-serif;
  font-weight: normal;
  font-size: 16px;
  line-height: 27px;
  color: #3e3e3e;

  scroll-behavior: smooth;

  &__body {
    margin: 0;
  }

  &__section {
    margin-bottom: 64px;

    @include onDesktop {
      margin-bottom: 104px;
    }

    &-title {
      font-weight: bold;
      font-size: 36px;
      line-height: 36px;
      color: #000;
      text-align: center;
      letter-spacing: -1.5px;

      &::after {
        content: "";
        display: block;
        width: 23px;
        height: 16px;
        margin: 4px auto 0;
        background-image: url(../images/crown.svg);
        background-position: center;
        background-size: contain;
        background-repeat: no-repeat;
      }
    }
  }
}
